@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
.App {
  text-align: center;
  max-width: 600px;
  border: 2px solid gainsboro;
  padding: 20px;
  min-height: 100vh;
}
.error {
  color: red;
}
.loading {
  color: cadetblue;
}
.pagination-button {
  padding: 5px 10px;
  margin: 4px;
  background-color: lightblue;
  border: 1px solid gainsboro;
  border-radius: 8px;
  cursor: pointer;
}

.people_card {
  text-align: left;
  margin: 20px 0px;
  padding: 15px 20px;
  border: 1px solid gainsboro;
  border-radius: 5px;
  box-shadow: 4px 4px 5px gainsboro;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
.infos p {
  margin-top: 10px;
  color: grey;
  font-size: 14px;
}
.category {
  align-items: center;
  width: 100px;
  text-align: center;
  border-radius: 1.5rem;
  background-color: gainsboro;
  padding: 8px 0;
}

.searchbar {
  min-width: 100%;
  border: none;
  box-shadow: 2px 2px 3px grey;
  border: 1px solid rgba(128, 128, 128, 0.459);
  height: 30px;
  padding: 5px;
  max-width: 600px;
  border-radius: 6px;
}
.searchbar:focus {
  background-color: aliceblue;
  transition: ease-out;
  transition-duration: 0.5s;
}

.singleDetails {
  text-align: left;
}
.vehicles {
  margin: 20px 0;
  color: grey;
  font-weight: normal;
}
.vehicle {
  list-style-type: none;
}

body {
  margin: auto;
  font-family: "Ubuntu", sans-serif, -apple-system, BlinkMacSystemFont,
    "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  max-width: 600px;
  margin-top: 5%;
  margin-bottom: 5%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0%;
  padding: 0%;
}

a {
  text-decoration: none;
  color: black;
}
a:hover {
  text-decoration: underline;
}

